import { removeItem } from "@/utils/storage";
const navMixin = {
  data() {
    return {
      navs: {
        home: {
          path: '/home',
          name: ''
        },
        chat: {
          path: '/chat',
          name: ''
        },
        game: {
          path: '/game',
          name: ''
        },
        blog: {
          path: '/blog',
          name: ''
        }
      }
    };
  },
  methods: {
    goRoute(path) {
      this.$router.push(path);
    },
    signOut(path) {
      this.$store.commit('CLEAR_USER_INFO');
      removeItem("userAccount", true);
      this.$router.push(path);
    }
  }
}

export { navMixin };